.statistics {
    position: relative;
    display: flex;
    width: 336px;
    height: 164px;
    margin-bottom: 40px;
    border-radius: 5px;
    box-shadow: -3px 5px 12px -3px rgba(0, 0, 0, 0.75);
}

.title {
    position: absolute;
    left: 50%;
    bottom: 50%;
    transform: translate(-50%, -50%);
    font-size: 16px;
    color: #95979c;
    text-transform: uppercase;
}

.statList {
    position: absolute;
    bottom: 0;
    display: flex;
    width: 100%;
    height: calc(40%);
    padding: 0;
    margin: 0;
}

.item {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: calc(25%);
    padding: 10px;
    margin: 0;
}
